import Vue from 'vue'
import App from './App.vue'
// 引入路由
import router from './router'

import Axios from 'axios'
// Vue.prototype.$axios = Axios 
//全局注册，使用方法为:this.$axios
Vue.prototype.$http = Axios //挂载到全局

import { apis } from './config'
Vue.prototype.apis = apis

Vue.config.productionTip = false


//网站中，登录页是首页，登录时发起第一次请求，但登录请求不应该该拦截，登录请求也不需要带有token，这才符合逻辑
// 进到路由才能发起请求，所以是路由守卫先工作，然后请求拦截才工作
//请求拦截，发起请求前拦截检查，把token放在请求头发送
Axios.interceptors.request.use(
    config => {
        /*8080/#/*/
        //基础头url
        //只有在有token时，才在请求中添加token，登录请求发送时还没有token，所以不添加token
        if (sessionStorage.getItem("token")) { // 请求拦截对登录请求豁免（不加token），因为发送登录请求时，token 还没有被赋值，所以if 里的语句不执行

            // `baseURL` 将自动加在 `url` 前面，除非 `url` 是一个绝对 URL
            // config.baseURL = 'http://47.106.64.140:8007'
            let token = sessionStorage.getItem("token")
            let userId = sessionStorage.getItem("userId")
            console.log("****************token********************")
            console.log(token);
            console.log("****************userId********************")
            console.log(userId);
            console.log("************************************")

            if (token) {
                config.headers.token = token
            }
            if (userId) {
                config.headers.userId = userId;
            }
        }
        return config;
    },
    error => {
        return Promise.reject(error);
    }
)


//响应拦截
Axios.interceptors.response.use(
    response => { //对响应数据做点什么
        console.log("响应拦截启动了");
        return response;
    },
    error => {
        if (error.response) {
            switch (error.response.status) {
                case 401: //返回 401 清除token信息并跳转到登录页面
                    window.sessionStorage.clear();
                    this.$router.push({ path: '/login' })
                    break;
            }
        }
        return Promise.reject(error.response.data);
    }
)



new Vue({
    router, // 路由注入到根实例中
    render: h => h(App), //App.vue是new vue根实例（也是根组件）的一个子组件，在index.html 中以 <App/> 被使用，并作为内容容器
}).$mount('#app')