<template>
  <div id="search_item">
     <el-row type="flex" class="row-bg" justify="center">
  <el-col :span="7"><h1 class="title">校园二手</h1></el-col>

  <el-col :span="11">
    <el-form :inline="true" >
      <!-- 搜素框 -->
 <el-form-item >
    <el-input class="search_input" v-model="searchInput" placeholder="搜点什么吧" ></el-input>
  </el-form-item>
<!-- 按钮 -->
      <el-form-item>
    <el-button class='search_btn' type="primary" @click="starSearch(searchInput)">搜索</el-button>
  </el-form-item>

     <div class="hot_key">
            <a href="#" @click='changeItem("数码电子")'>数码电子</a>
            <a href="#" @click='changeItem("服饰美容")'>服饰美容</a>
            <a href="#" @click='changeItem("体育器械")'>体育器械</a>
            <a href="#" @click='changeItem("休闲食品")'>休闲食品</a>
        </div>
  </el-form>
  </el-col>
</el-row>

<!-- 导航栏 -->
<el-row class="navigation_row" id="navigation-bg" >
  <el-col :span="11" :offset='4'><ul id="navigation_Ul">
    <li><router-link to="/homePage" @click.native='changeGoodsType("所有商品")'>所有商品</router-link></li><span>|</span>
      <li><router-link to="/homePage/electronicProducts" @click.native='changeGoodsType("电子产品")'>电子产品</router-link></li><span>|</span>
       <li><router-link to="/homePage/books" @click.native='changeGoodsType("书籍教材")'>书籍教材</router-link></li><span>|</span>
       <li><router-link to="/homePage/sportGoods" @click.native='changeGoodsType("体育用品")'>体育用品</router-link></li><span>|</span>
          <li><router-link to="/homePage/lostThingsDisplay" @click.native='changeGoodsType("招领启事")'>招领启事</router-link></li>
    </ul></el-col>
  <el-col :span="6">
   <div class="login_part">
    <span>{{showUserName+' '}}您好，欢迎光临！</span> 
      <!-- <router-link to="/login">请登录</router-link> <router-link to="/adminOperation">免费注册</router-link>  -->
      <router-link to="/userCenter">个人中心</router-link>
      <router-link to="" class="log_out" @click.native='logout'>退出登录</router-link>
</div>
  </el-col>
</el-row>

<div class="goods_type_div"><h2 class="goods_type" id="name"><span> {{goodsType}}</span></h2></div>

  </div>
</template>

<script>
export default {
    data() {
    return {
      searchInput: '',
       goodsType:'所有商品',
       showUserName:'',
    }
  },
   beforeCreate() {
    // console.log('导航栏的 beforeCreate 钩子');
    
  },
   created() {
    // console.log('导航栏的 created 钩子');
    this.showUserName=sessionStorage.getItem("userName");// 获取用户名
  },
  beforeMount() {
    // console.log('导航栏的 beforeMount 钩子');
    
  },
  mounted() {
    // console.log('导航栏的 mounted 钩子');
    
  },
  methods:{
      changeItem: function(searchInput) {
            this.searchInput = searchInput;
            // this.searchWeather();
        },
      changeGoodsType:function(inputType){
        // console.log('点击招领启事了');
        this.goodsType=inputType;
        console.log('点击类型是',this.goodsType);
      },
      starSearch:function(searchInputText){
            this.goodsType = '搜索结果';
           console.log('开始搜索');
        // this.$router.push('/homePage/displaySearchResult');
        this.$router.push({path:"/homePage/displaySearchResult",query:{searchText:searchInputText}});

      },
      logout(){
           sessionStorage.clear();//清除token
      this.$router.push('/login');
      },
  }

}
</script>

<style scoped>
 /* .el-row {
  margin-bottom: 20px;
} */

/* .el-row:last-child {
  margin-bottom: 0;
} */
  .el-col {
    border-radius: 4px;
  }
 
  .row-bg {
    padding: 10px 0;
    /* background-color: #f9fafc; */
    background-color: #eeeeee;

  }

.title{
    text-align: center;
     color: rgb(92, 91, 91);

  }

/* el-row 和el-form-item 都默认有下边距，所以要消除 */
#search_item .el-form-item,
#search_item .el-row{
  margin-bottom: 0px;
}

#search_item .search_input{
    width: 500px;
    margin-top: 20px;
  }

#search_item .search_btn{
    margin-top: 20px;
  }

#search_item .hot_key{
  margin-top: 5px;
}
#search_item .hot_key a {
    text-decoration: none;
    /* color: black; */
    margin-right: 5px;
     color: rgb(68, 68, 68);

}

#navigation-bg{
  background-color: #409EFF;
}
/* 用来放导航栏的链接，li元素在一行上 */
#search_item #navigation_Ul li{
display: inline-block;
}
#search_item .navigation_row span{
margin-right: 10px;
margin-left: 10px;
}

.navigation_row .router-link-active{
    text-decoration: none;
    color: white;
}

.navigation_row .router-link-active:hover{
  color: rgb(168, 11, 11);
}

.navigation_row{
  color: white;
}

.login_part{
  margin-top: 15px;
}


/* 因为 router-link 会转化成a标签，所以对 router-link 应用的样式还要再对a标签应用一遍 ，这样才能使router-link变化前后的样式一样*/
#navigation-bg a{
    text-decoration: none;
    color: white;
}

#navigation-bg a:hover{
  color: rgb(168, 11, 11);

}

#search_item .goods_type{
  margin: 5px;
  margin-left: 293px;
     color: rgb(92, 91, 91);

}

.log_out{
  margin-left: 20px;
}
</style>
