<template>
  <div id="gooods_detail">
    <navigationSearch/> 
    <el-row  type="flex" justify="center" >
  <el-col :span="13" >
      <div>
  <h2 class="goods_detial_title">商品详情</h2>
  <!-- 卡片组件默认设置了padding -->
 <el-card class="box-card" shadow="always">
     <div class="img_desc_div">
     <!-- 左边的图片区域 -->
 <div class="goodsImg_div"><img :src="goodsInfo.goodsImgUrl" alt=""></div>
 <!-- 右边的商品详情区域 ,这里用了float 方法来让一行并列两个元素，其实更简单的方法就是用两个span就可以，外面套个div就可以了-->
 <div class="goods_desc">
     <span class="span1_3">物品详情：</span>
     <!-- <el-tag type="info" class="span1_3">物品详情</el-tag> -->
     <div class="desc_div">{{goodsInfo.goodsDescription}}</div>
     <span class="span2">售价：</span>
     <div><span class="rmb_sign">￥</span><span class="goods_price">{{goodsInfo.goodsPrice}}</span></div>
     <span class="span1_3">联系方式：</span>
     <div>{{goodsInfo.phone}}</div>
 </div>
 </div>
</el-card>
      </div>
      </el-col>
</el-row>
  </div>
</template>

<script>
import navigationSearch from '../components/navigation-seach'

export default {
  components: {
   navigationSearch,
  },
   data() {
       return{
           goodsInfo:{}
       }
   },
    created () {
      // 接收product-list.vue传过来的goodsId，一点击，马上进行路由跳转，路由跳转后到新组件，created() 函数会先创建，然后模板才渲染成html
      let receiveGoodsId = this.$route.query.goodsId
        this.$message.success('物品id是：'+receiveGoodsId);

        this.$http.get(this.apis.find_goods_by_id+'?goodsId='+receiveGoodsId).then(res=>{
      console.log(res);
      if (res.data.code===200){
        console.log("物品详情接收到数据")
        this.goodsInfo=res.data.data;
         
      }
    }).catch(error=>{
      console.log("物品详情请求失败");
      console.log(error)
    })
  },
}
</script>

<style scoped>
/* detail组件里用了公共的navigation组件，但在显示这个组件的时候想隐藏公共组件的分类标题 */
#gooods_detail >>> .goods_type_div{
    display: none;
}

#gooods_detail .goods_detial_title{
  margin: 5px;
  margin-left: 7px;
  color: rgb(92, 91, 91);


}

/* 这是卡片的样式 */

  /* .box-card {
    width: 480px;
  } */

/* 容器变成bfc，让图片的容器浮动后依然撑起卡片的高度 */
  .img_desc_div{
    overflow: hidden;

  }

/* 设置左边的图片展示部分 */
  .goodsImg_div {
    height: 300px;
    width: 48%;
    float: left;
    margin-right: 20px;
    /* 在自己的内容区域四周增加内边距，它的内容是img，也就是在img的四周增加空白 ，
    因为div 的 width 固定了，再增加就会使div的实际宽度更大*/
    padding:10px 10px 10px 10px;
    /* border:1px solid #BFBFBF; */
    border: 1px solid rgb(228, 227, 227);
    box-shadow:2px 2px 3px #aaaaaa;
    /* box-shadow:-2px -2px 3px #aaaaaa, 2px 2px 3px #aaaaaa; */
    margin-bottom: 5px;

    
  }
  .goodsImg_div img{
    height: 100%;
    /* div 的宽度width是用来放img元素的，所以img的width最大可以占满父元素width的100% 。div的width是要放东西的，放什么呢，就是放img元素*/
    width: 100%;
  }

/* 右边部分 */
/* 右边的容器变成bfc */
.goods_desc{
    overflow: hidden;
     /* color: rgb(131, 130, 130); */
     /* color: rgb(114, 113, 113); */
     color: rgb(92, 91, 91);
font-size: 19px;

}
  .span1_3{
      float: left;
    font-weight: bold;

  }

  .goods_desc>div{
        overflow: hidden;
        margin-bottom: 20px;
  }

 .span2{
      float: left;
      margin-left: 31px;
      margin-top: 2px;
    font-weight: bold;

    }

    .rmb_sign,
    .goods_price{
    color: rgb(177, 6, 6);
    }

.rmb_sign{
     font-size: 17px;
}
    .goods_price{
         font-size: 26px;
    font-weight: bold;
    }

    .desc_div{
      height: 110px;
    }
</style>
